import React from 'react';
import './section8.css';
import nft from '../img/v12.png';
import feature1 from '../img/feature1.png';
import feature2 from '../img/feature2.png';
import feature3 from '../img/feature3.png';
import feature4 from '../img/feature4.png';
import feature5 from '../img/feature5.png';
import feature6 from '../img/feature6.png';
import headFeature from '../img/head-feature.png';
import Grid from '@material-ui/core/Grid';
  



function Section8() {
  
  return (
  <>
  <div id='section8'  className='section8'>
    <h1 className='nft-head'>GENESIS BLOCK MINERS CLUB<br />
<span className='nft-sub-head'>NFT</span></h1>
      <Grid container spacing={0}>
        <Grid item xs={12} sm={6} md={6} >
            <div className='section82'>
                
                 <div container className='nftContainer'>
                   <img className='nft-img' src={nft} />
                 </div>   
            </div>
        </Grid>
        <Grid item xs={12} sm={6} md={6} >
            <div className='section81'>
              
              <div className='headIMG-features'>
                <img className='img-head-feature' src={headFeature} />
              </div>
              <div className='featureContainer'>
                <Grid className='featureContainer' container spacing={0}>
                   <Grid item xs={4} sm={4} md={4} >
                     <img data-aos='zoom-in' className='img-feature' src={feature1} />
                   </Grid> 
                   <Grid item xs={4} sm={4} md={4} >
                     <img data-aos='zoom-out' className='img-feature' src={feature2} />
                   </Grid> 
                   <Grid item xs={4} sm={4} md={4} >
                     <img data-aos='zoom-in' className='img-feature' src={feature3} />
                   </Grid> 
                   <Grid item xs={4} sm={4} md={4} >
                     <img data-aos='zoom-out' className='img-feature top-feature' src={feature4} />
                   </Grid> 
                   <Grid item xs={4} sm={4} md={4} >
                     <img data-aos='zoom-in' className='img-feature top-feature' src={feature5} />
                   </Grid> 
                   <Grid item xs={4} sm={4} md={4} >
                     <img data-aos='zoom-out' className='img-feature top-feature' src={feature6} />
                     <div className='section81btn'>
                <a className='btnsection8'>Know more</a>
              </div>
                   </Grid>
                   
                </Grid>   
                
              </div>   
             
            </div>
            
        </Grid>
      </Grid> 
    </div>

    <div className='mob-sec-8'>
    <h1 className='nft-head'>GENESIS BLOCK <br /><span class='subNftHead'> MINERS CLUB</span><br />
<span className='nft-sub-head'>NFT</span></h1>
            <div className='section82'>
               
            <div container className='nftContainer'>
                   <img className='nft-img' src={nft} />
                 </div>
            </div>
            <div class="horizontalMobileScroll">
                <div class="horizontalMobileScroll__cell">
                <img className='img-feature' src={feature1} />
                </div>

                <div class="horizontalMobileScroll__cell">
                <img className='img-feature' src={feature2} />
                </div>

                <div class="horizontalMobileScroll__cell">
                <img className='img-feature' src={feature3} />
                </div>

                <div class="horizontalMobileScroll__cell">
                <img className='img-feature' src={feature4} />
                </div>

                <div class="horizontalMobileScroll__cell">
                <img className='img-feature' src={feature5} />
                </div>

                <div class="horizontalMobileScroll__cell">
                <img className='img-feature' src={feature6} />
                </div>

            </div>
    </div>
</>
  );
}

export default Section8;
