import React, {useEffect} from 'react';
import './section4.css';
import img41 from '../img/img41.png';
import img42 from '../img/img42.png';
import img43 from '../img/img43.png';
import img44 from '../img/img44.png';
import img45 from '../img/img45.png';
import img46 from '../img/img46.png';
import img47 from '../img/img47.png';
import img48 from '../img/img48.png';
import Grid from '@material-ui/core/Grid';
import Aos from 'aos';
import 'aos/dist/aos.css';




function Section4() {
    useEffect(()=> {
        Aos.init({duration: 1000});
      }, []);
  
 
    return  (
       <>
            <div className='section4'>
                <div className='section4container1'>
                    <div className='centerGrid4'>
                            <Grid container spacing={0}>            
                                    <Grid item xs={12} sm={6} md={6} >
                                        <div   className='section41'>
                                            <h1  className='line1'>The Worlds First </h1>
                                            <h1 className='line2'>Decentralised Marketplace</h1>
                                            <h1  className='line3'> for Supercar Fractional Ownership</h1>
                                            <img  className='img41' src={img41}/>
                                        
                                        </div>
                                    </Grid>
                                    <Grid item xs={12} sm={6} md={6}>
                                        <div className='section42'>
                                            <div className='img45container'>
                                                <img  className='img45' src={img45}/>
                                            </div>
                                        
                                        </div>
                                    </Grid>
                            </Grid>  
                  </div>
                
                <Grid container spacing={0} className='section4-grid'>
                    <Grid className='gridCurrent' item xs={12} sm={6} md={6} >
                        <h1 className='currentAsset'>Current Asset Pools</h1>
                       <img  className='img42' src={img42} />
                    </Grid>
                    <Grid className='gridComing' item xs={12} sm={6} md={6}>
                        <h1 className='comingSoon'>Coming Soon</h1>
                                    <Grid container spacing={2}>
                                        <Grid item xs={1} sm={1} md={1}>
                                        </Grid>
                                        <Grid className='g2' item xs={2} sm={2} md={2}>
                                            <img  className='img48' src={img48} />
                                        </Grid>
                                        <Grid className='g2' item xs={2} sm={2} md={2}>
                                            <img  className='img47' src={img47} />
                                        </Grid>
                                        <Grid className='g2' item xs={2} sm={2} md={2}>
                                            <img  className='img46' src={img46} />
                                        </Grid>
                                        <Grid   className='g2' item xs={2} sm={2} md={2}>
                                            <img  className='img43' src={img43} />
                                        </Grid>
                                        <Grid   className='g2' item xs={2} sm={2} md={2}>
                                            <img  className='img44' src={img44} />
                                        </Grid>
                                        <Grid item xs={1} sm={1} md={1}>
                                        </Grid>
                                    </Grid>
                    </Grid>
                </Grid>        

               
                                   
                </div>
            </div>
     
       </>
  );
}

export default Section4;
