import React, {useEffect} from 'react';
import './section7.css';
import section7 from '../img/section7img.png';
import section7mob from '../img/section7imgmob.png';
import Aos from 'aos';
import 'aos/dist/aos.css';




function Section7() {
  useEffect(()=> {
    Aos.init({duration: 1000});
  }, []);
  
  
  return (
    <>
  
    <div id='section7'  className='section7'>
      <h1 className='token-utilisation'>
      TOKEN<span style={{color: '#4489F1'}}>UTILISATION</span>
      </h1>
      <p className='para-tokenUtilisation'>The community drives the protocol through the DAO. Weighted averages of holders are taken into account<br /> in the decision making tree and The DAO controls the protocol, making the long term holding benefits of the<br /> tokens deeper for those with greater exposure on the assets. The tokens carry staking rewards for delegation<br /> and are widely accepted across the platform for the various services Boit Club provides.</p>
    <div data-aos='fade-right' className='tokenUtilisationImg'>
      <img className='section7img' src={section7} />
    </div>
    <div  className='tokenUtilisationImgmob'>
      <img className='section7imgmob' src={section7mob} />
    </div>
    </div>
    </>
  );
}

export default Section7;
