import React, {useEffect} from 'react';
import './highlights.css';
import Grid from '@material-ui/core/Grid';
import im1 from '../img/im-1.png';
import im2 from '../img/im-2.png';
import im3 from '../img/im-3.png';
import im4 from '../img/im-4.png';
import im5 from '../img/im-5.png';
import im6 from '../img/im-6.png';
import im7 from '../img/im-7.png';
import g1 from '../img/g1.png';
import g2 from '../img/g2.png';
import g3 from '../img/g3.png';
import g4 from '../img/g4.png';
import g5 from '../img/g5.png';
import g6 from '../img/g6.png';
import g7 from '../img/g7.png';
import toke from '../img/toke.png';
import Aos from 'aos';
import 'aos/dist/aos.css';
  

function Highlight() {
  useEffect(()=> {
    Aos.init({duration: 1000});
  }, []);
  return (
    
<>


    <div id='highlishts'   className='highlishts'>
       <div className='HighlightContainer'>
       <Grid container spacing={0}>
              <Grid  item xs={3} md={3} sm={3}>
                <div className='high'>
                    <img className='toke' src={toke} />
                </div>
                
              </Grid>
              <Grid  item xs={3} md={3} sm={3}>
              <div className='iconContainer'>
                  <img className='icon' src={im1} />
                </div>
                <div className='content'>
                  <img className='imgContent' src={g1} />  
                </div>
              </Grid>
              <Grid  item xs={3} md={3} sm={3}>
              <div className='iconContainer'>
                  <img className='icon' src={im2} />
                </div>
                <div className='content'>
                <img className='imgContent' src={g2} />
                </div>
              </Grid>
              <Grid  item xs={3} md={3} sm={3}>
              <div className='iconContainer'>
                  <img className='icon' src={im3} />
                </div>
                <div className='content'>
                <img className='imgContent' src={g3} />
                </div>
              </Grid>
       </Grid>   
       <Grid container spacing={0} style={{marginTop: '3rem'}}>
              <Grid  item xs={3} md={3} sm={3}>
              <div className='iconContainer'>
                  <img className='icon' src={im4} />
                </div>
                <div className='content'>
                <img className='imgContent' src={g4} />
                </div>
              </Grid>
              <Grid  item xs={3} md={3} sm={3}>
              <div className='iconContainer'>
                  <img className='icon' src={im5} />
                </div>
                <div className='content'>
                <img className='imgContent' src={g5} />
                </div>
              </Grid>
              <Grid  item xs={3} md={3} sm={3}>
              <div className='iconContainer'>
                  <img className='icon' src={im6} />
                </div>
                <div className='content'>
                <img className='imgContent' src={g6} />
                </div>
              </Grid>
              <Grid  item xs={3} md={3} sm={3}>
              <div className='iconContainer'>
                  <img className='icon' src={im7} />
                </div>
                <div className='content'>
                <img className='imgContent' src={g7} />
                </div>
              </Grid>
       </Grid>      
       </div>
    </div>



   <div className='mobile-highlishts'>
      <div className='headingHighlightMobile'>
        <h1 data-aos='fade-up' className='headmobilehighlisht'>Token <span style={{color: '#ffffff'}}>Highlights</span></h1>
      </div>
      <div className='highlightContainermobile'>
        <Grid container spacing={0}>
              <Grid  item xs={3} md={3} sm={3}>
                  <img  className='im1' src={im1} />
              </Grid>
              <Grid  item xs={9} md={9} sm={9}>
                  <h1  className='itm itm1'><span style={{fontWeight: 'Bold'}}>Invest</span> in fractions of a high-<br />yield Supercars.<br />Anywhere in the world. </h1>
              </Grid>
              <Grid  item xs={3} md={3} sm={3}>
                  <img  className='im2' src={im2} />
              </Grid>
              <Grid  item xs={9} md={9} sm={9}>
                  <h1  className='itm itm2'><span style={{fontWeight: 'Bold'}}>EARN</span> when that asset is<br /> used/rented/salvaged.ii   </h1>
              </Grid>
              <Grid item xs={3} md={3} sm={3}>
                  <img  className='im3' src={im3} />
              </Grid>
              <Grid item xs={9} md={9} sm={9}>
                  <h1  className='itm itm3'><span style={{fontWeight: 'Bold'}}>STAKE</span> RWA LP Contracts to<br /> farm more $BOIT</h1>
              </Grid>
              <Grid item xs={3} md={3} sm={3}>
                  <img  className='im4' src={im4} />
              </Grid>
              <Grid item xs={9} md={9} sm={9}>
                  <h1  className='itm itm4'><span style={{fontWeight: 'Bold'}}>TRANSFER</span> long <span style={{fontWeight: 'Bold'}}>term</span> fractions<br /> on the marketplace to<br /> potential short term investors.</h1>
              </Grid>
              <Grid  item xs={3} md={3} sm={3}>
                  <img  className='im5' src={im5} />
              </Grid>
              <Grid item xs={9} md={9} sm={9}>
                  <h1  className='itm itm5'><span style={{fontWeight: 'Bold'}}>USE</span> the asset in real life by<br /> paying with LP tokens.</h1>
              </Grid>
              <Grid  item xs={3} md={3} sm={3}>
                  <img  className='im6' src={im6} />
              </Grid>
              <Grid  item xs={9} md={9} sm={9}>
                  <h1 className='itm itm6'><span style={{fontWeight: 'Bold'}}>VALIDATE</span> transactions on the<br /> Boit Chain and earn tokens by<br /> setting up a validator node</h1>
              </Grid>
              <Grid item xs={3} md={3} sm={3}>
                  <img  className='im7' src={im7} />
              </Grid>
              <Grid item xs={9} md={9} sm={9}>
                  <h1  className='itm itm7'>Your fraction ownership gives<br /> you access to the Boit DAO<br /> for all decision making<br /> regarding the asset. </h1>
              </Grid>
        </Grid> 
      </div>        
    </div>
 
</>     
  );
}

export default Highlight;
