import * as React from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import Grid from '@material-ui/core/Grid';
import './faq.css';





export default function ControlledAccordions() {
  
  const [expanded, setExpanded] = React.useState('panel1');
  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <div className='faq'>
      <div className='faqTextHeading'>
        <h1 className='headFaqText'>FAQ's</h1>
      </div>
      <Accordion expanded={expanded === 'panel1'} onChange={handleChange('panel1')} className="faq-header">
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1bh-content"
          id="panel1bh-header"
          className='headerBoxFaq'
        >
          <Typography sx={{ width: '100%', flexShrink: 0 }} className="faqhead">
          What is a Boit Club?
          </Typography>
         
        </AccordionSummary>
        <AccordionDetails>
          <Typography  className="faqhead">
          Boit Club, a technology platform founded in 2017, started off as a P2P network that brings alternative use to idle luxury assets, specifically Supercars due to their highly unutilised nature of consumption of the products life cycle. BOIT soon became a widespread network of enthusiasts, asset owners and affiliate vendors across the globe. In 2020, BOIT added Private Jets to the network and never turned back. BOIT now boasts one of the largest networks of supercars, private jets and yachts across the globe. Assets that generate yield by way of rentals, events, driving tours, track days and other alternative uses for the same assets. BOIT hosts several events that engage with existing supercar owners, enthusiasts and passionate individuals that all share this common interest for upper crest living, while being environmentally and financially conscious. BOIT has now developed the world’s first Decentralised Exchange (DEX) for Real World Assets, that generate yield. and by enabling an entire investor ecosystem to be able to leverage the benefits of Web3.0 and completely revolutionize the way people own and use assets.

          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel2'} onChange={handleChange('panel2')} className="faq-header">
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2bh-content"
          id="panel2bh-header"
          className='headerBoxFaq'
        >
          <Typography sx={{ width: '100%', flexShrink: 0 }} className="faqhead">
          What is the $BOIT Token?
          </Typography>
         
        </AccordionSummary>
        <AccordionDetails>
          <Typography  className="faqhead">
          $BOIT is an integral part of our RWA (Real-World-Assets) Defi Protocol, enabling retail and institutional investors to participate in their favorite Businesses, specially their favorite assets, at a much deeper, transparent and controlled level. By leveraging the power of blockchain technology, we have designed a platform that allows for retail level investing to fractionalised, yield generating institutional assets. The Ethereum smart contracts standard is used to enable a fully decentralized ecosystem and then the substantial benefit of using the Layer 2 solution provided by Polygon (Formerly MATIC) to reduce transactional fees while using the platform. 
<br /> <br />The $BOIT native token is built on the Polygon and will feature a Dapp that will give its users the ability to take/offer exposure on fractionalised, yield generating assets for a committed period of time. By exposing traditional assets to decentralized finance, we bring a revolution in consumption patterns, utility and a clear disruption to traditional ownership models. The $BOIT token will be used for governance, staking rewards as well as actual trade in for services across the BOIT club platform. The tokens will also be used while paying transaction fees on the Boit Platform. 

          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel3'} onChange={handleChange('panel3')} className="faq-header">
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3bh-content"
          id="panel3bh-header"
          className='headerBoxFaq'
        >
          <Typography sx={{ width: '100%', flexShrink: 0 }} className="faqhead">
          Ownership Perks?
          </Typography>
        
        </AccordionSummary>
        <AccordionDetails>
          <Typography  className="faqhead">
          $BOIT holders will be able to interact with native smart contracts running on BOIT’s 3 different platforms that can be accessed via the main portal www.boitclub.io/ . Being a dual sided business, Institutions and businesses are needed to mint their own equity tokens for which liquidity needs to be provided within BOIT. Those Equity tokens can then be used to mint Asset tokens, which provide for collateral for those Assets within those primary Companies. This would ensure consistent and growing demand for $BOIT with time. As the value of assets get added to the protocol, the liquidity pools on the grow proportionately, so will the amount of $BOIT vested within those liquidity pools, eventually driving the price in correlation to the demand of $BOIT.
<br /> <br />$BOIT holders and investors, apart from being exposed to the natural increase in value due to the deflationary design of the token, will be able to use the token itself to use services across the BOIT platform. Services like listing an asset, creating a new liquidity pool, trading of fractions, exchanging a fraction for actual use of the asset,  or even holding tokens to gain access to the DAO, which gives direct control over how the protocol is run. $BOIT will also offer time-locked staking allowing for yield farming and Liquidity Provider benefits. The multi layers of exposure allow for investors with various appetites take up various scales of exposure.

          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel4'} onChange={handleChange('panel4')} className="faq-header">
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3bh-content"
          id="panel3bh-header"
          className='headerBoxFaq'
        >
          <Typography sx={{ width: '100%', flexShrink: 0 }} className="faqhead">
          When did the project come to life?
          </Typography>
        
        </AccordionSummary>
        <AccordionDetails>
          <Typography  className="faqhead">
          Boit Club was founded in 2017 and the services have been growing since. The $BOIT token project https://www.boitclub.io/ was conceptualized in February 2021 and put into action right after. With careful consideration given to every aspect of the business, the products were developed and tested in Beta conditions within a small community of users and vendors. The initial focus was to develop a vendor channel that was crypto friendly. BOIT started accepting crypto payments on their platform https://boit.club in May 2021. With a fast spreading network, BOIT’s network remains the largest rental network that is crypto-enabled and growing with members opting for crypto payments on a regular basis.

          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion expanded={expanded === 'panel5'} onChange={handleChange('panel5')} className="faq-header">
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel5bh-content"
          id="panel5bh-header"
          className='headerBoxFaq'
        >
          <Typography sx={{ width: '100%', flexShrink: 0 }} className="faqhead">
          When is the launch?
          </Typography>
        
        </AccordionSummary>
        <AccordionDetails>
          <Typography  className="faqhead">
          There are various stages to the $BOIT token launch and availability. There will be a closed Presale, followed by a private VC sale, followed by a Genesis Community Sale for early stage investors, followed by a Public IDO and then Central Exchange listing.

          </Typography>
        </AccordionDetails>
      </Accordion>

    </div>
  );
}
