import React, {useEffect, useRef } from 'react';
import '../App.css';
import './HeroSection.css';
import tm from '../img/mobile-text.png';
import textmain from '../img/Text.png';
import Video from '../img/engine.mp4';
import Video2 from '../img/engine2.mp4';
import Grid from '@material-ui/core/Grid';
import Aos from 'aos';
import 'aos/dist/aos.css';


  




function HeroSection() {
  const videoEl = useRef(null);
  const attemptPlay = () => {
    videoEl &&
      videoEl.current &&
      videoEl.current.play().catch(error => {
        console.error("Error attempting to play", error);
      });
  };
  const videoE1 = useRef(null);
  const attemptPlay1 = () => {
    videoE1 &&
      videoE1.current &&
      videoE1.current.play().catch(error => {
        console.error("Error attempting to play", error);
      });
  };


 
  useEffect(()=> {
    Aos.init({duration: 1000});
    attemptPlay();
    attemptPlay1();
  }, []);
  return (



<>
    
    <div id='herosection' className='hero-container'>
  
  
     
     

      <div className="diiv" style={{ width: '100%', padding: '30px', zIndex: '1' }}>
        <Grid container spacing={3}>
           <Grid data-aos='fade-right' item xs={12} md={6} sm={6}>
              <div className='DivGif'>
                  <div className='textmain'>
                      <img className='textMain' src={textmain} alt="text" />
                  </div>
                
             </div>
          </Grid>
           
           <Grid  item xs={12} md={6} sm={6}>
             
                   <div className='engine-box'>
                  <video className='videoFrame' playsInline loop muted alt="All the devices"  ref={videoEl} src={Video}  /> 
            </div>
           </Grid>
         </Grid>
      </div>
        
    
      
    
          
    </div>
    <div id='herosection-mob' className='hero-container-mob'>
  <div className="diiv" style={{ width: '100%', padding: '30px', zIndex: '1' }}>
    <Grid container spacing={3}>
       <Grid  item xs={12} md={6} sm={6}>
          <div className='DivGif'>
            <div className='engine-box'>
                  <video className='videoFrame' playsInline loop muted alt="All the devices"  ref={videoE1} src={Video2}  /> 
            </div>
            
         </div>
      </Grid>
       
       <Grid data-aos='fade-left' item xs={12} md={6} sm={6}>
              <div className='textmain'>
                  <img className='textMain' src={tm} alt="text" />
              </div>
             
       </Grid>
     </Grid>
  </div>
    

  

      
</div>
     {/* <div className="rectangle-50" style={{ width: '100%', zIndex: '1' }}>
      <Grid className="container-rectangle"  container spacing={5}>
        <Grid data-aos="fade-right" item xs={12} md={3} sm={3}>
          <div className="h3h">
          <h3 className="h3">Total Supply</h3>
          </div>
          <Button className="button2" variant="outlined"  style={{backgroundColor: 'black', color: 'white', borderRadius: '50px', borderColor: 'white', width: '219px', marginTop: '20px', marginBottom: '20px' }}><h5 className="h5">100,000,000</h5></Button>
         </Grid>
         
        <Grid data-aos="fade-right" item xs={12} md={3} sm={3}>
        <div className="h3h">
         <h3 className="h3">Holders</h3>  
         </div>
         <Button className="button2" variant="outlined" style={{backgroundColor: 'black', color: 'white', borderRadius: '50px', borderColor: 'white', width: '219px', marginTop: '20px', marginBottom: '20px' }}><h5 className="h5">204</h5></Button>
        </Grid>
        <Grid data-aos="fade-left" item xs={12} md={3} sm={3}>
        <div className="h3h">
          <h3 className="h3">Price</h3>
          </div>
        <Button className="button2" variant="outlined"  style={{backgroundColor: 'black', color: 'white', borderRadius: '50px', borderColor: 'white', width: '219px', marginTop: '20px', marginBottom: '20px' }}><h5  className="h5">0.1 USD</h5></Button>
        </Grid>
        <Grid data-aos="fade-left" item xs={12} md={3} sm={3}>
        <div className="h3h">
          <h3 className="h3">Burned</h3>
          </div>
        <Button className="button2" variant="outlined"  style={{backgroundColor: 'black', color: 'white', borderRadius: '50px', borderColor: 'white', width: '219px', marginTop: '20px', marginBottom: '20px' }}><h5  className="h5">1,234</h5></Button> 
         </Grid>
       </Grid>
  </div>*/}
 
    </>

  );
}

export default HeroSection;
