import React, {useEffect} from 'react';
import './tokenOmics.css';
import Aos from 'aos';
import 'aos/dist/aos.css';
import { makeStyles} from '@material-ui/core/styles';
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";

const useStyles = makeStyles({
 
  tableRow: {
    "&:last-child td, &:last-child th": {
      
      backgroundColor: "black"
    },
    backgroundColor: '#3A343C',
    "&:hover": {
      backgroundColor: "#3C1958 !important"
    },
    height: 6
  },
 
 
});

function createData(TokenAllowcation, Allowcation, Supply) {
  return { TokenAllowcation, Allowcation, Supply };
}

const rows = [
  createData("Presale", "2.00%", "20,000,000"),
  createData("Seed Investor", "5.00%", "50,000,000"),
  createData("Genesis Community Sale", "3.24%", "32,395,000"),
  createData("Public IDO", "1.50%", "15,000,000"),
  createData("CEX Liquidity", "2.00%", "20,000,000"),
  createData("Boit Foundation", "20.00%", "200,000,000"),
  createData("Dev Allocation", "10.00%", "100,000,000"),
  createData("Marketing", "12.00%", "120,000,000"),
  createData("Advisors", "2.57%", "25,685,000"),
  createData("Strategic Partnership", "3.00%", "30,000,000"),
  createData("Vendor Network", "10.00%", "100,000,000"),
  createData("Airdrops", "0.10%", "1,020,000"),
  createData("Partner Allocation", "0.09%", "9,00,000"),
  createData("Staking Rewards", "28.50%", "285,000,000"),
  createData("", "100%", "1,000,000,000")
];




function Table1() {
  useEffect(()=> {
    Aos.init({duration: 1000});
  }, []);
  const classes = useStyles();
  
  return (
    <>




   
    
      <TableContainer>
          <Table className='table1' aria-label="simple table">
            <TableHead className='tableBG'>
              <TableRow>
              <TableCell className='tableHead1'>Token Allocation</TableCell>
              <TableCell className='tableHead1'>Allocation</TableCell>
              <TableCell className='tableHead1'>Supply</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => (
              <TableRow
              
              className={classes.tableRow}
              onClick={() => {}}
            >
              <TableCell component="th" scope="row" className='tableCell1'  >
                {row.TokenAllowcation}
              </TableCell>
              <TableCell className='tableCell1' >{row.Allowcation}</TableCell>
              <TableCell className='tableCell1'  >{row.Supply}</TableCell>
            </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
   
       

    </>
  );
}
export default Table1;











