import React, {useEffect, useRef} from 'react';
import './tokenOmics.css';
import Token from '../img/tokenVideo.mp4';
import 'aos/dist/aos.css';
import Table1 from './table1';
import Grid from '@material-ui/core/Grid';
import ControlledAccordions from './faq-main';
import Table2 from './table2';


  



function Tokenomics() {
  const videoRef = useRef(null);

  useEffect(() => {
    let options = {
      rootMargin: "0px",
      threshold: [0.0, 0.1]
    };

  

    let handlePlay = (entries, observer) => {
      entries.forEach((entry) => {
        console.log(entry);
        if (entry.isIntersecting) {
          console.log('TRUE');
          videoRef.current.play();
        } else {
          videoRef.current.pause();
        }
      });
    };

    let observer = new IntersectionObserver(handlePlay, options);

    observer.observe(videoRef.current);
 
  });
    return (
      <>
       
      
        <div id="tokenomics" className='cards'>   
        <div className='head-tokenomics'>TOKEN<span style={{color: '#4489F1'}}>NOMICS</span></div>
          <div className="tokenomicsBox">
              <Grid container spacing={0}>
                  <Grid item xs={12} sm={6} md={6} >
                    <div className='table-tokenomics'>
                    <Table1 />

                    </div>
                  
                  </Grid>
                  <Grid className="divmob" item xs={12} sm={6} md={6} >
                    <div className="chartContainer">
                  
                     <video  className='tokenVideo' muted playsInline autoPlay  ref={videoRef} src={Token}  /> 
                    </div>  
                  </Grid>
              </Grid>   
          </div> 
        </div> 
        <div className='pumponomics' >
          <Grid container spacing={0}>
              <Grid item xs={12} sm={6} md={6} >
                <div className='table-dropdown'>
                <ControlledAccordions />
                </div>
                
              </Grid>
              <Grid item xs={12} sm={6} md={6} >
                <div className='headPumponomics'>
                <h1 className='head-pumponomics'>PUMPA<span style={{color: '#4489F1'}}>MENTALS</span></h1>
                <p className='sub-Head-pumponomics'>Token Metrics</p>
                </div>
               <div className='table-2'>
                 
               <Table2 />

               </div>
                
              </Grid>
          </Grid>   
        </div>
      </>
    );
  }


export default Tokenomics;
