import React, {useEffect} from 'react';
import './tokenOmics.css';
import Aos from 'aos';
import 'aos/dist/aos.css';
import { makeStyles } from '@material-ui/core/styles';
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";

const useStyles = makeStyles({
 
  tableRow: {
    "&:last-child td, &:last-child th": {
      
      backgroundColor: "black"
    },
    backgroundColor: '#3A343C',
    "&:hover": {
      backgroundColor: "#3C1958 !important"
    },
    height: 6
  },
 
  
 
});

function createData(Name, Details) {
  return { Name, Details };
}

const rows = [
  createData("Token Name", "BOIT"),
  createData("Token Type", "ERC20 (Polygon)"),
  createData("Total Supply", "1,000,000,000"),
  createData("Presale $", "225,000"),
  createData("Seed $", "1,000,000"),
  createData("Genesis Community Sale $", "1,255,000"),
  createData("Public IDO $", "1,000,000"),
  createData("Estimate Listing Value $", "0.10"),

];




function Table2() {
  useEffect(()=> {
    Aos.init({duration: 1000});
  }, []);
  const classes = useStyles();
  
  return (
    <>




   
    
      <TableContainer>
          <Table className='table2' aria-label="simple table">
            <TableHead className='tableBG'>
              <TableRow >
              
              <TableCell align="center" colSpan={12}  className='tableHead2'>
              Summary
              </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {rows.map((row) => (
              <TableRow
              
              className={classes.tableRow}
              onClick={() => {}}
            >
              <TableCell component="th" scope="row" className='tableCell2'  >
                {row.Name}
              </TableCell>
              <TableCell className='tableCell2'  >{row.Details}</TableCell>
            </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
   
       

    </>
  );
}
export default Table2;











