import React, { useState, useEffect } from 'react';
import Tooltip from "@material-ui/core/Tooltip";
import { Link } from 'react-router-dom';
import './Navbar.css';
import docs from '../Docs/Boit Whitepaper.pdf';
import logoBig from '../img/logo-big.png'
import iconWallet from '../img/iconWallet.png';
import { ethers } from "ethers";
import ENS,{ getEnsAddress } from '@ensdomains/ensjs'


// const signer = provider.getSigner();

function Navbar() {
  const [click, setClick] = useState(false);
  const [button, setButton] = useState(true);
  const [accounts, setAccount] = useState("")
  
  const callPhaseOne = async () => {
    const provider = new ethers.providers.Web3Provider(window.ethereum);
    const ens = new ENS({ provider , ensAddress:getEnsAddress('1')})
    try {
      const [account] = await window.ethereum.request({
        method: "eth_requestAccounts",
      });
      console.log(getEnsAddress);
      setAccount(account);
    } catch (error) {
      console.log(error);
    }
  }

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);
  const [bal, setBal] = useState(0);

  const showButton = () => {
    if (window.innerWidth <= 960) {
      setButton(false);
    } else {
      setButton(true);
    }
  };

  useEffect(() => {
    showButton();
  }, []);

  window.addEventListener('resize', showButton);

  return (
    <>

      <nav className='navbar'>
        <div className='navbar-container'>
          <Link to='/' className='navbar-logo' onClick={closeMobileMenu}>
            <img className='logo-big' src={logoBig} alt="Logo" /></Link>
          <div className='menu-icon' onClick={handleClick}>
            <i className={click ? 'fas fa-times' : 'fas fa-bars'} />
          </div>
          <ul className={click ? 'nav-menu active' : 'nav-menu'}>
            <li className='nav-item'>
              <Link to='/' className='nav-links' onClick={closeMobileMenu}>
                Home
              </Link>
            </li>
            <li className='nav-item'>

              <Tooltip
                title="Coming Soon"
                placement="top"
              >
                <a
                  href='#dex'
                  className='nav-links'
                  onClick={closeMobileMenu}
                  style={{ cursor: 'default' }}
                >Buy $BOIT </a>
              </Tooltip>
            </li>
            <li className='nav-item'>
              <a
                href='https://boit.club/'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Boit Club
              </a>
            </li>
            <li className='nav-item'>
              <Link
                to='/'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                NFT
              </Link>
            </li>

            <li className='nav-item'>
              <Link
                to={docs}
                className='nav-links'
                onClick={closeMobileMenu}
                target="_blank" download
              >
                Whitepaper
              </Link>
            </li>
            <li className='nav-item'>
              {/* <label htmlFor="">balance : <span>{bal}</span></label> <br /> */}
              <a
                // href='#tokenomics'
                className='wallet'
                onClick={() => callPhaseOne()} //call phaseOne
              >
                {accounts.length > 0 ? `${accounts[0]}${accounts[1]}${accounts[2]}${accounts[3]}${accounts[4]}...${accounts[30]}${accounts[31]}${accounts[32]}` : "Connect Wallet"}<span> <img className='icon-wallet' src={iconWallet} /> </span>
              </a>
            </li>

          </ul>

        </div>
      </nav>

    </>
  );
}

export default Navbar;